.form {
  position: relative;
  z-index: 3;
  padding: 36px 48px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.12);
  border-radius: 8px 8px 0 0;
  background-color: #fff;

  &__title {
    font-size: 36px;
    font-weight: bold;
    color: #000;
    margin-bottom: 38px;
  }

  &__sub-title {
    font-size: 12px;
    font-weight: bold;
    color: $form-sub-title-color;
    margin-bottom: 8px;
  }

  &__list {
    position: relative;
    display: block;
    width: 320px;
    margin-bottom: 24px;

    border: 1px solid $form-item-border-color;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 11px 16px;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 2px;
      height: 100%;
      background: #5a6bff;
      border-radius: 8px 0 0 8px;
      opacity: 0;
      transition: 0.2s;
    }

    &:focus-within {
      &:after {
        opacity: 1;
      }
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__item {
    width: 100%;
    height: 100%;
    border: none;
    font-family: $main-fonts;
    font-size: 16px;
    color: $form-item-text-color;
    transition: 0.2s;
  }

  &__input {
    @include style-text-placeholder;
  }

  &__textarea {
    height: 115px;
    resize: none;
    @include style-text-placeholder;
  }

  &__btn {
    margin-top: 34px;
    transition: box-shadow 0.3s;

    &:hover,
    &:focus {
      box-shadow: 0 0 10px rgba(0,0,0,0.5);
      color: #fff;
    }
  }

  @media (max-width: 768px) {
    margin: auto;
  }

  @media (max-width: 450px) {
    width: 95%;
    padding: 36px 30px;
    &__list {
      width: 100%;
    }
  }
}

.hidden-wrapper {
  position: relative;
  &:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.6);
  }
}

.hide-btn-elem {
  display: none;
}
